import React from 'react';
import ProtoTypes from 'prop-types';

import './transaction.module.css';

const Transaction = ({ type, amount, currency }) => {
        return (<tr className="tableString">
            <td>{type}</td>
         <td>{ amount}</td>
         <td>{ currency}</td>
    </tr>)
}



const TransactionHistory = ({ items }) => {
    return (
        <table className="transaction-history">
  <thead>
    <tr>
      <th>Type</th>
      <th>Amount</th>
      <th>Currency</th>
    </tr>
            </thead>
        <tbody>
        
            {items.map(item => (
              <Transaction type={item.type} amount={item.amount} currency={item.currency} key={ item.id}/>
                
               ))}
       
            </tbody>
        
        </table>

    )
}

TransactionHistory.protoTypes = {
  items: ProtoTypes.arrayOf(
    ProtoTypes.exact({
      id: ProtoTypes.string.isRequired,
      type: ProtoTypes.string.isRequired,
      amount: ProtoTypes.string.isRequired,
      currency: ProtoTypes.string.isRequired,

    })
  )
}


export default TransactionHistory